import { db, storage } from '../firebase';
import { doc, getDoc, setDoc, updateDoc } from 'firebase/firestore';
import { ref, uploadBytes, getDownloadURL } from 'firebase/storage';

export interface BrandingConfig {
  logoUrl?: string;
  updatedAt?: number;
}

const BRANDING_DOC = doc(db, 'siteContent', 'branding');

export async function getBrandingConfig(): Promise<BrandingConfig> {
  try {
    const snap = await getDoc(BRANDING_DOC);
    if (!snap.exists()) return {};
    return (snap.data() as BrandingConfig) || {};
  } catch (e: any) {
    // بعض الصفحات العامة قد لا تمتلك صلاحية قراءة وثيقة العلامة التجارية حسب القواعد
    // نتعامل بالرقة ونعيد قيمة افتراضية بدون كسر الواجهة
    if (e && typeof e === 'object' && (e.code === 'permission-denied' || e.message?.includes('Missing or insufficient permissions'))) {
      console.warn('brandingService: read branding config denied for public page');
      return {};
    }
    console.warn('brandingService: failed to read branding config', e);
    return {};
  }
}

export async function setBrandingConfig(config: BrandingConfig): Promise<void> {
  const payload: BrandingConfig = {
    ...config,
    updatedAt: Date.now(),
  };
  await setDoc(BRANDING_DOC, payload, { merge: true });
}

export async function uploadLogoAndSave(file: File): Promise<string> {
  const path = `branding/logo_${Date.now()}_${file.name}`;
  const storageRef = ref(storage, path);
  await uploadBytes(storageRef, file);
  const url = await getDownloadURL(storageRef);
  await setBrandingConfig({ logoUrl: url });
  return url;
}

export async function getLogoUrl(): Promise<string | undefined> {
  const config = await getBrandingConfig();
  return config.logoUrl;
}